/* Modal */
.modal__overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  background-color: rgba(197, 213, 230, 0.5);
  visibility: hidden;
}

.modal__overlay.show {
  visibility: visible;
}

.modal {
  visibility: hidden;
  opacity: 0;
  transform: rotate(20deg);
  transition: all 0.2s ease-in-out;
}

.modal.active {
  visibility: visible;
  opacity: 1;
  transform: rotate(0);
}

.modal input[type="text"],
.modal input[type="email"],
.modal input[type="password"],
.modal input[type="tel"] {
  border: none;
  outline: none;
  padding: 5px 10px;
