/* Vars */

/* Typography */
$font-family-base: Geometria, Arial, sans-serif;
$heading-font-family: $font-family-base; // Replace heading font
$ff: $font-family-base;
$font-size: 14px;
$fz: $font-size;
$line-height: 1.3;
$lh: $line-height;
$letter-spacing: normal;
$ls: $letter-spacing;

/* Layout */
$wrapper-size: 1140px;

$break-lg: 1200px;
$break-md: 992px;
$break-sm: 768px;
$break-xs: 576px;

/* Colors */
$primary: #9c5d9a;
$secondary: #3d176b;
$third-color: #939aa3;
$four-color: #c5d5e6;
$five-color: #f3f7fa;
$light: #fff;
$dark: #000;
$header-color: #363b5d;
$text-color: #363b5d;
$bg-color: $light;
$link-color: $primary;
$link-hover-color: darken($link-color, 15%);
